.sidebar-container {
  /* flex: 1; */
  width: 280px;
  /* height: calc(100vh-50px); */
  /* background-color: rgb(240, 239, 239); */
  background-color: white;

  position: sticky;
  top: 50px;
  border-right: 1px solid #e2e8f0;

  hr {
    height: 1px; /* 기본 높이 */
    border: none; /* 기본 테두리 제거 */
    border-top: 1px solid lightgray; /* 상단 테두리 두께와 색상 조절 */
    width: 90%;
  }
}

.sidebar-center {
  display: flex;
  justify-content: center;
}

.sidebar {
  padding: 20px;
  padding-top: 2rem;
  color: #8c8c8c;
}

.sidebar-header {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
  font-family: 'om';
  font-weight: bold;
  font-size: 50px;
  color: #a9c1e3;
  cursor: pointer;
}

.sidebar-menu {
  margin-bottom: 2rem;
  font-family: 'sunB';
}

.sidebar-title {
  font-size: 0.9rem;
  color: rgb(197, 197, 197);
}

.sidebar-list {
  list-style: none;
  padding: 0.5rem;
  /* padding-top: 0; */
  font-family: 'sunB';
}

.sidebar-list-item {
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;
  font-size: 1.5rem;
  margin-bottom: 4%;
  font-size: 20px;
}
.sidebar-list-item.active,
.sidebar-list-item:hover {
  /* background-color: rgb(212, 209, 235); */
  background-color: #e2e8f0;
}